body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: left;
}

.vertical-align {
  display: flex;
  align-items: center;
}

.logo {
  width: 100%;
}

.left-align {
  display: flex;
  text-align: left;
}

.gray {
  color: gray;
}

.black {
  color: black;
}

#listserv_signup_nopad {
  position: relative;
  width: 100%;
  min-height: 1px;
  padding-right: 15px;
  padding-left: 0px;
}

.cardinal {
    background-color: #8c1515;
}
